<template>
  <div class="step-list">
    <div v-for="obj in data" v-bind:key="obj.stageId">
      <div class="item" :class="[obj.stageId === expandId_ ? 'active' : '']" @click="expandItem(obj.stageId)">
        <i v-if="obj.stageId === expandId_" class="el-icon-caret-bottom"></i>
        <i v-else class="el-icon-caret-right"></i>
        <span>{{obj.stageName}}</span>
        <span class="pull-right" >
          <i class="iconfont iconduihao" v-show="iconduihaoShow"></i>
          <i class="iconfont iconshijian" v-show="iconshijianShow"></i>
        </span>
      </div>
      <div class="itemContent" v-show="obj.stageId === expandId_">
        <div v-for="child in obj.steps" v-bind:key="child.stepId" class="item" :class="[(obj.stageId === expandId_ && child.stepId === currentId) ? 'active' : '']" @click="activeItem(child.stepId, obj.stageId)">
          <span>{{child.stepName}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'TeamStepList',
  props: {
    expandId: Number,
    activeId: Number,
    data: Array,
    iconduihaoFlg: {
      default: false
    },
    iconshijianFlg: {
      default: false
    }
  },
  data () {
    return {
      expandId_: this.expandId,
      currentId: this.activeId,
      iconduihaoShow: this.iconduihaoFlg,
      iconshijianShow: this.iconshijianFlg
    }
  },
  methods: {
    expandItem (id) {
      if (this.expandId_ === id) {
        this.expandId_ = -1
      } else {
        this.expandId_ = id
      }
    },
    activeItem (id, parentId) {
      this.expandId_ = parentId
      this.currentId = id
    }
  }
}
</script>
<style scoped lang="scss">
  .step-list{
    font-size:14px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:#666666;
    padding-bottom:20px;
    background: #FFFFFF;
    .item{
      padding: 12px 20px 12px 15px;
      cursor:pointer;
      .pull-right{
        i{
          margin-right:6px;
        }
      }
    }
    .itemContent{
      .item{
        padding: 10px 39px 10px 36px;
        cursor:pointer;
      }
      .active{
        background: #F8F8F8;
        color:#6612CF;
      }
    }
  }
</style>
