<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import TeamStepList from '../../../../components/school-training/TeamStepList.vue'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    TeamStepList
  },
  mounted () {
    this.initEvent()
    this.initData()
  },
  data () {
    return {
      doUpload: '/api/up/file',
      pppss: {
        srid: ''
      },
      winWidth: window.innerWidth,
      data2: [{
        name: '执行任务',
        url: ''
      }, {
        name: '任务单',
        url: 'work-order.html'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html'
      }, {
        name: '竞速排名',
        url: ''
      }, {
        name: '励英社区',
        url: ''
      }],
      data3: [{ name: '成果物点评' }, { name: '指派任务' }, { name: '队友进行状态' }],
      currentStageId: -1,
      currentStepId: -1,
      taskData: [],
      stepData: null,
      commentContent: '',
      isShow: false,
      commentDetailedIsShow: false,
      showTapName: '成果物点评',
      isReviewData: true,
      state: 1,
      appointListData: [],
      isTeamLeader: 0,
      memberStatusData: [],
      stepInfo: null
    }
  },
  methods: {
    initEvent: function () {
      let me = this
      me.winWidth = $('body').width()
      window.onresize = function () {
        me.winWidth = $('body').width()
      }
    },
    initData: function () {
      this.loadStepListData()
      this.loadAppointListData()
      this.loadMemberStatusData()
      this.loadStepinfo()
    },
    loadStepListData () {
      let me = this
      this.$http.post('/doproject/steplist', {
        'teamid': 12
      }, res => {
        if (res.success) {
          me.stepData = res.data.stages
          me.currentStageId = res.data.currentStageId
          for (let obj of me.stepData) {
            if (obj.stageId === me.currentStageId) {
              me.currentStepId = obj.steps[0].stepId
            }
          }
          console.log(me.currentStepId)
        }
      })
    },
    loadAppointListData () {
      let me = this
      this.$http.post('/doproject/appointstatus', {
        'teamid': 1,
        'stepid': 1
      }, res => {
        if (res.success) {
          me.appointListData = res.data.status
          me.isTeamLeader = res.data.teamLeader
        }
      })
    },
    loadMemberStatusData () {
      let me = this
      this.$http.post('/doproject/memberstatus', {
        'teamid': 1,
        'stepid': 1
      }, res => {
        if (res.success) {
          me.memberStatusData = res.data
        }
      })
    },
    loadStepinfo () {
      let me = this
      this.$http.post('/doproject/stepinfo', {
        'teamid': 1,
        'stepid': 1
      }, res => {
        if (res.success) {
          me.stepInfo = res.data
          if (me.stepInfo.type === 1) {
            me.data3 = [{ name: '成果物点评' }, { name: '指派任务' }, { name: '队友进行状态' }]
          } else {
            me.data3 = [{ name: '成果物点评' }, { name: '队友进行状态' }]
          }
        }
      })
    },
    sizeChange (size) {
      return this.winWidth * size / 1920 + 'px'
    },
    clickStep (data) {
      console.log(data)
    },
    writeRely () {
      let me = this
      me.isShow = !me.isShow
    },
    showCommentDetailed () {
      let me = this
      me.commentDetailedIsShow = !me.commentDetailedIsShow
    },
    activeItem (index, name) {
      this.showTapName = name
    },
    appointMember (obj) {
      obj.status = 1
    },
    saveAppoint () {
      let me = this
      let userCodes = []
      for (let user of this.appointListData) {
        if (user.status === 1) {
          userCodes.push(user.userCode)
        }
      }
      if (userCodes.length > 0) {
        me.$alert('任务指派后将无法修改，是否进行保存', '提示', {
          confirmButtonText: '确定',
          callback: action => {
            me.$http.post('/doproject/appointstep', {
              'teamid': 1,
              'stepid': 1,
              'users': userCodes
            }, res => {
              if (res.success) {
                me.$message('保存成功')
              }
            })
          }
        })
      }
    },
    playVideo (video) {
      window.open('video-player.html?video=' + video, 'video-player')
    },
    downloadTemplate (name) {
      // let me = this
      this.$http.post('/doproject/output/downloadtemplate', {
        'filename': name
      }, res => {
        if (res.success) {
          console.log(res.data)
        }
      })
    }
  }
}
</script>

